import React from 'react';
import Home from '../components/Home';
import About from '../components/About';
import Contact from '../components/Contact';
import NotFound from '../components/NotFound';
import MakeChange from '../components/CardDesk/MakeChange';
import CollatzConjecture from '../components/CardDesk/CollatzConjecture';
import CalculateDistance from '../components/CardDesk/CalculateDistance';

const routes = [
  {
    path: '/',
    exact: true,
    main: () => <Home />,
  },
  {
    path: '/about',
    exact: false,
    main: () => <About />,
  },
  {
    path: '/contact',
    exact: false,
    main: () => <Contact />,
  },
  {
    path: '/make-change',
    exact: true,
    main: () => <MakeChange />,
  },
  {
    path: '/collatz-conjecture',
    exact: true,
    main: () => <CollatzConjecture />,
  },
  {
    path: '/calculate-distance',
    exact: true,
    main: () => <CalculateDistance />,
  },
  {
    path: '',
    exact: false,
    main: () => <NotFound />,
  },
];

export default routes;
